import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HttpClientService } from '../service/http-client.service';
import { ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { formatDate } from '@angular/common';
import * as moment from 'moment'

@Component({
  selector: 'app-edit-patient',
  templateUrl: './edit-patient.component.html',
  styleUrls: ['./edit-patient.component.css']
})
export class EditPatientComponent implements OnInit {

  notes: FormGroup;
  general: FormGroup;
  appointments: FormGroup;
  visits: FormGroup;
  patient: any = {
    id: '',  
    firstName: '' , 
    lastName: '',
    sex: '',
    status: '',
    dob: '',
    general: {}
  };
  patientId: string;
  doctorId: string;
  list: any;
  SERVER_URL = "http://localhost:3000/api/v1/addphoto";
  URL: any = "http://localhost:3000/photos";
  uploadForm: FormGroup;
  today= new Date();
  blood: any = ['A+', 'A-', 'B+','B-','O+','O-','AB+','AB-'];
  type: any = ['Admission','New Visit'];
  visitType: any = ['Admission','Follow Up'];
  reason: any = ['Lip','Palate','Face','Head'];
  doctor: any;

  constructor(private formBuilder: FormBuilder, private modalService: NgbModal,
    private httpClientService: HttpClientService, private route: ActivatedRoute,
     private httpClient: HttpClient) { 
      let date: Date = new Date();  
      console.log("Date = " + date); 
      console.log(this.today);
     }

  ngOnInit(): void {
    this.patientId = this.route.snapshot.params.id
    this.getPatientId(this.patientId);
    this.createContactForm();
    this.createGeneralForm();
    this.createAppointmentForm();
    this.createVisitForm();
    this.createUploadForm();
    this.getDoctor();
  }

  dateFormatter(e){
    console.log(e);
    e.target.setAttribute(
      "data-date",
      moment(e.target.value, "YYYY-MM-DD")
      .format(e.target.getAttribute("data-date-format"))
  )
    moment("2021-03-01", "YYYY-MM-DD").format("DD MMM YYYY")
  }

  getDoctor(): void{
    this.httpClientService.getAllDoctorDetails().subscribe(data => {
        this.doctor = data.result,
       console.log(this.doctor);
      });
  }

  ChangeDoctor1(e) {
    this.doctorId=e.target.value;
    this.appointments.patchValue({
      with:e.target.value
    })
    console.log(this.doctorId);
   }

  ChangeDoctor(e) {
    this.doctorId=e.target.value;
    this.visits.patchValue({
      examiner:e.target.value
    })
    console.log(this.doctorId);
   }

  getPatientId(id): void {
    this.httpClientService.getPatientById(id).subscribe(data => {
      this.patient = data.result,
        console.log(this.patient);
        if(this.patient.general){
        this.list = this.patient.general,
        this.general.patchValue({
          ...this.patient.general
        })
      }
      this.appointments.patchValue({
        location:this.patient.programSite,
        //startDate: this.today
      })
      this.visits.patchValue({
        location:this.patient.programSite
      })
        console.log(this.list);
    });
  }

  // Notes Form
  createContactForm() {
    this.notes = this.formBuilder.group({
      //note: "",
      previousSurgery:false,
      previousSurgeryNote:"",
      cold: false,
      coldNote:"",
      cyanosis: false,
      cyanosisNote: "",
      bleeding: false,
      bleedingNote:"",
      breathing: false,
      breathingNote: "",
      currentMedicine: "",
      immunization: "",
      drug:false,
      drugNote:""
    });
  }

  onSubmit() {
    console.log('Your form data : ', this.notes.value);
    let notes = { id: this.patientId, notes: this.notes.value };
    console.log(notes);
    this.httpClientService.addNote(notes).subscribe(data => {
      console.log(data);
      this.ngOnInit();
    });
  }
  // General Form
  createGeneralForm() {
    this.general = this.formBuilder.group({
      //firstName: "",
      patientStatus: "",
      //middleName: "",
      externalPatientId: "",
      //lastName: "",
      blood: "",
      //sex: "",
      //clinicSite: "",
      //dob: "",
      refferedBy: "",
      refferedDate: ""
    });
  }

  onClick() {
    console.log('Form data : ', this.general.value);
    let generalform = { id: this.patientId, general: this.general.value };
    console.log(generalform);
    this.httpClientService.addGeneral(generalform).subscribe(data => {
      console.log(data);
      this.getPatientId(this.patientId);
    });
  }
  // Photos Form
  createUploadForm() {
    this.uploadForm = this.formBuilder.group({
      profile: [''],
      description: ''
    });
  }

  onFileSelect(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.uploadForm.get('profile').setValue(file);
    }
  }
  onSubmitPhoto() {
    const formData = new FormData();
    formData.append('id', this.patientId);
    formData.append('photo', this.uploadForm.get('profile').value);
    formData.append('description', this.uploadForm.get('description').value);
    let uploadForm = { id: this.patientId, uploadForm: this.uploadForm.value };
    console.log(uploadForm);
    this.httpClient.post<any>(this.SERVER_URL, formData).subscribe(
      (res) => {
        console.log(res)
        this.getPatientId(this.patientId);
      },
      (err) => console.log(err)
    );
    this.ngOnInit();
  }
  // Appointments Form
  createAppointmentForm() {
    this.appointments = this.formBuilder.group({
      startDate: "",
      //endDate: "",
      with: "",
      location: "",
      type: "",
      status: "",
      notes: "",
      //allDay: ""
    });
  }

  onSave() {
    console.log('Form data : ', this.appointments.value);
    let appointments = { id: this.patientId, with: this.doctorId,appointments: this.appointments.value };
    console.log(appointments);
    this.httpClientService.addAppointment(appointments).subscribe(data => {
      console.log(data);
      this.getPatientId(this.patientId);
      this.ngOnInit();
    });
  }
  // Visits Form
  createVisitForm() {
    this.visits = this.formBuilder.group({
      date: [formatDate(this.today, 'yyyy-MM-dd', 'en')],
      //dischargeDate: "",
      reason: "",
      examiner: "",
      location: "",
      visitType: "",
      //subjective: "",
      //objective: "",
      //assessment: "",
      //plan: ""
    });
  }

  onVisit() {
    console.log('Form data : ', this.visits.value);
    let visits = { id: this.patientId, visits: this.visits.value };
    console.log(visits);
    this.httpClientService.addVisit(visits).subscribe(data => {

      console.log(data);
      this.ngOnInit();
    });
  }

  openLg(content) {
    this.modalService.open(content, { size: 'lg' });
  }

  openLg1(content1) {
    this.modalService.open(content1, { size: 'lg' });
  }

  openLg2(content2) {
    this.modalService.open(content2, { size: 'lg' });
  }

  openLg3(content3) {
    this.modalService.open(content3, { size: 'lg' });
  }

}
