import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { User } from '../user';
import jwt_decode from 'jwt-decode';
import crypto from 'crypto-browserify';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  API_URL: string = 'http://localhost:3000/api/v1';
  headers = new HttpHeaders().set('Content-Type', 'application/json');
  currentUser = {};

  constructor(private httpClient: HttpClient,public router: Router){}

  register(user: User): Observable<any> {

    return this.httpClient.post(`${this.API_URL}/users/register`, user).pipe(
        catchError(this.handleError)
    )
  }

  login(user: User) {
    return this.httpClient.post<any>(`${this.API_URL}/login`, user)
      .subscribe((res: any) => {
        localStorage.setItem('access_token', res.token)
        this.router.navigate(['']);
        // this.getUserProfile(res._id).subscribe((res) => {
        //   this.currentUser = res;
        //   this.router.navigate(['' + res.msg._id]);
        // })
      })
  }

  getAccessToken() {
    return localStorage.getItem('access_token');
  }

  isLoggedIn(): boolean {
    let authToken = localStorage.getItem('access_token');
    return (authToken !== null) ? true : false;
  }

  logout() {
    if (localStorage.removeItem('access_token') == null) {
      this.router.navigate(['users/login']);
    }
  }

  getDecodedAccessToken(): any {
    try{
        return jwt_decode(this.getAccessToken());
    }
    catch(Error){
        return null;
    }
  }

  getPatload(data){
    return this.httpClient.get<any>(`${this.API_URL}/dec/${data}`)
      
  }

  // getPayload(data:any){
  //   try{
  //     var mykey = crypto.createDecipher('aes-128-cbc', 'Hms@123');
  //   var mystr = mykey.update(data, 'hex', 'utf8')
  //   mystr += mykey.final('utf8');
  //   console.log("str",mystr)


  //   let buff = Buffer.from(mystr, "base64")
  //   let text = buff.toString("utf8");
  //   console.log("data",JSON.parse(text))

  // }
  // catch(Error){
  //     return null;
  // }
  // }

  getUserProfile(id): Observable<any> {
    return this.httpClient.get(`${this.API_URL}/users/profile/${id}`, { headers: this.headers }).pipe(
      map((res: Response) => {
        return res || {}
      }),
      catchError(this.handleError)
    )
  }

  handleError(error: HttpErrorResponse) {
    let msg = '';
    if (error.error instanceof ErrorEvent) {
      // client-side error
      msg = error.error.message;
    } else {
      // server-side error
      msg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(msg);
  }
}
