import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Room } from '../json/room';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-add-room',
  templateUrl: './add-room.component.html',
  styleUrls: ['./add-room.component.css']
})
export class AddRoomComponent implements OnInit {

  patient: any;
  patientId: string;
  _id: string;
  list: any;

  constructor(private httpClientService:HttpClientService, private route: ActivatedRoute,
    public router: Router) { }

  ngOnInit(): void {
    this.getPatient();
  }

  getPatient(): void{
    this.httpClientService.getAllPatientDetails().subscribe(data => {
      this.patient = data,
     console.log(this.patient);
     this.list = this.patient.result,
       console.log(this.list);
    });
  }

  userModel = new Room('', '', '', '', '', '','');
  ChangePatient(e) {
   this.patientId=e.target.value;
  }

  onSubmit(){
    console.log(this.userModel);
    let notes = { patientId: this.patientId, ... this.userModel };
    console.log(notes);
    this.httpClientService.addRoomById(notes).subscribe(data => {
         
      console.log(data);
      this.router.navigate(['roomlist']);
     });;
  }

}
