import { Component, OnInit } from '@angular/core';
import { Doctor } from '../json/doctor';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-room-allotment',
  templateUrl: './room-allotment.component.html',
  styleUrls: ['./room-allotment.component.css']
})
export class RoomAllotmentComponent implements OnInit {

  patient: any;
  list: any;

  constructor(private httpClientService:HttpClientService) { }

  ngOnInit(): void {
    this.getRoomList();
  }

  getRoomList(): void{
    this.httpClientService.getRoomList().subscribe(data => {
      this.patient = data,
     console.log(this.patient);
     this.list = this.patient.result,
       console.log(this.list);
    });
  }

}
