import { Component, OnInit } from '@angular/core';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-counseling',
  templateUrl: './counseling.component.html',
  styleUrls: ['./counseling.component.css']
})
export class CounselingComponent implements OnInit {

  doctor: any;
  list: any;

  constructor(private httpClientService:HttpClientService) { }

  ngOnInit(): void {
    this.getDoctor();
  }

  getDoctor(): void{
    this.httpClientService.getAllCounseling().subscribe(data => {
        this.doctor = data.result,
       console.log(this.doctor);
      });
  }

}
