import { Component, OnInit } from '@angular/core';
import { Doctor } from '../json/doctor';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-surgical-list',
  templateUrl: './surgical-list.component.html',
  styleUrls: ['./surgical-list.component.css']
})
export class SurgicalListComponent implements OnInit {

  patient: any;
  list: any;

  constructor(private httpClientService:HttpClientService) { }

  ngOnInit(): void {
    this.getSurgicalList();
  }

  getSurgicalList(): void{
    this.httpClientService.getSurgicalList().subscribe(data => {
      this.patient = data,
     console.log(this.patient);
     this.list = this.patient.result,
       console.log(this.list);
    });
  }

}
