import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Pediatric } from '../json/pediatric';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-lab-details',
  templateUrl: './lab-details.component.html',
  styleUrls: ['./lab-details.component.css']
})
export class LabDetailsComponent implements OnInit {

  patient: any;
  patientId: string;
  labId:string;
  visitId:string;
  list: any;
  SERVER_URL = "http://localhost:3000/api/v1/savelabandmove";
  uploadForm: FormGroup;

  constructor(private httpClientService: HttpClientService, private route: ActivatedRoute,
    public router: Router,private formBuilder: FormBuilder,private httpClient: HttpClient) { }

  ngOnInit(): void {
    this.patientId = this.route.snapshot.params.id
    this.labId=this.route.snapshot.params.labId;
    this.visitId=this.route.snapshot.params.visitId;
    this.getAllPatientApi(this.patientId);
    if(this.labId!="0"){
this.getLabById( this.labId);
    }
    this.createUploadForm();
  }

  getAllPatientApi(id) {
    this.httpClientService.getAllPatientApi(id, this.visitId).subscribe(data => {
      this.patient = data.result,
      console.log(this.patient);
      this.list = this.patient.visits;
      console.log(this.list);
    });
  }

  userModel = new Pediatric('', '', '', '', '', '',
  '','', '','', '','');

  getLabById(id){
    this.httpClientService.getLabById(id).subscribe(data => {
      this.userModel = data.result,
        console.log(this.userModel);
    });
  }

  // Photos Form
  createUploadForm() {
    this.uploadForm = this.formBuilder.group({
      profile: [''],
      description: ''
    });
  }

  onFileSelect(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.uploadForm.get('profile').setValue(file);
    }
  }
  onSubmitPhoto() {
    const formData = new FormData();
    formData.append('patientId', this.patientId);
    formData.append('visitId',this.visitId);
    formData.append('photo', this.uploadForm.get('profile').value);
    formData.append('description', this.uploadForm.get('description').value);
    formData.append('sts', "M");
    let uploadForm = { patientId: this.patientId,visitId: this.visitId, sts: 'M', uploadForm: this.uploadForm.value };
    console.log(uploadForm);
    this.httpClient.post<any>(this.SERVER_URL, formData).subscribe(
      (res) => {
        console.log(res)
        this.getAllPatientApi(this.patientId);
      },
      (err) => console.log(err)
    );
    this.router.navigate(['surgicallist']);
  }

  onReject(){
    const formData = new FormData();
    formData.append('patientId', this.patientId);
    formData.append('visitId',this.visitId);
    formData.append('photo', this.uploadForm.get('profile').value);
    formData.append('description', this.uploadForm.get('description').value);
    formData.append('sts', "M");
    let uploadForm = { patientId: this.patientId,visitId: this.visitId, sts: 'R', uploadForm: this.uploadForm.value };
    console.log(uploadForm);
    this.httpClient.post<any>(this.SERVER_URL, formData).subscribe(
      (res) => {
        console.log(res)
        this.getAllPatientApi(this.patientId);
      },
      (err) => console.log(err)
    );
    this.router.navigate(['counseling']);
  }

}
