import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Operation } from '../json/operative';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-operation-edit',
  templateUrl: './operation-edit.component.html',
  styleUrls: ['./operation-edit.component.css']
})
export class OperationEditComponent implements OnInit {

  patient: any;
  patientId: string;
  operativePlanId:string;
  constructor(private httpClientService: HttpClientService, private route: ActivatedRoute,
    public router: Router) { }

  ngOnInit(): void {
    this.patientId = this.route.snapshot.params.id;
    this.operativePlanId=this.route.snapshot.params.operativePlanId;
    this.getPatientId(this.patientId);
    if(this.operativePlanId!="0"){
this.getOperativeById( this.operativePlanId);
    }
  }

  getPatientId(id): void {
    this.httpClientService.getPatientById(id).subscribe(data => {
      this.patient = data.result,
        console.log(this.patient);
    });
  }

  userModel = new Operation('','', '', '', '', '', '');

  getOperativeById(id){
    this.httpClientService.getOperativeById(id).subscribe(data => {
      this.userModel = data.result,
        console.log(this.userModel);
    });
  }

  onSubmit(){
    console.log(this.userModel);
    let notes = { patientId: this.patientId, ... this.userModel };
    console.log(notes);
    this.httpClientService.addOperativeById(notes).subscribe(data => {

      console.log(data);
    });
  }
  onSave() {
    let id = this.patientId;
    console.log(id);
    this.httpClientService.moveToSurgical(id).subscribe(data => {
      console.log(data);
      this.router.navigate(['surgicallist']);
     });
  }

  onReject(){
    let move = { id: this.patientId, visitId: this.patient.processId, status: 'ALR'};
    this.httpClientService.processComplete(move).subscribe( data => {
      console.log(data);
    });
    this.router.navigate(['counseling']);
  }

}
