import { Component, OnInit } from '@angular/core';
import { CalendarOptions } from '@fullcalendar/angular';
import { HttpClientService } from '../service/http-client.service';
declare var $: any;
@Component({
  selector: 'app-theatre-schedule',
  templateUrl: './theatre-schedule.component.html',
  styleUrls: ['./theatre-schedule.component.css']
})

export class TheatreScheduleComponent implements OnInit {

  patient: any;

  constructor(private httpClientService:HttpClientService) { }

  ngOnInit(): void {
    this.getTheatreSchedule();
 }

 calendarOptions: CalendarOptions = {
  initialView: 'dayGridMonth',
  headerToolbar: {
    left: 'prev,next today',
    center: 'title',
    right: 'dayGridMonth,timeGridWeek,timeGridDay,listWeek'
  }, // bind is important!
  events: [
    { title: 'event 1', date: '2021-04-01' },
    { title: 'event 2', date: '2021-04-02' }
  ]
};

getTheatreSchedule(){
  this.httpClientService.getTheatreList().subscribe(data => {
    this.calendarOptions.events = data.result,
   console.log(this.patient);
  });
}
 
  }
