import { Component, OnInit } from '@angular/core';
import { Patient } from '../json/patient';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-pediatric-list',
  templateUrl: './pediatric-list.component.html',
  styleUrls: ['./pediatric-list.component.css']
})
export class PediatricListComponent implements OnInit {

  patient: any;
  list: any;

  constructor(private httpClientService:HttpClientService) { }

  ngOnInit(): void {
    this.getPatient();
  }

  getPatient(): void{
    this.httpClientService.getPediatricList().subscribe(data => {
      this.patient = data,
     console.log(this.patient);
     this.list = this.patient.result,
       console.log(this.list);
    });
  }

}
