import { formatDate } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-add-patient',
  templateUrl: './add-patient.component.html',
  styleUrls: ['./add-patient.component.css']
})
export class AddPatientComponent implements OnInit {

  contactForm: FormGroup;
  submitted = false;
  sex: any = ['Male', 'Female', 'Transgender'];
  proofName: any = ['Passport', 'Driving License'];
  today= new Date();

  constructor(private formBuilder: FormBuilder,private httpClientService:HttpClientService,
    public router: Router) { }

  ngOnInit(): void {
    this.createContactForm();
  }

  calculateAge(e) {
    var dateString=e.target.value;
    var now: any = new Date();
            // var today = new Date(now.getYear(), now.getMonth(), now.getDate());

            var yearNow = now.getYear();
            var monthNow = now.getMonth();
            var dateNow = now.getDate();

            var dob: any = new Date(dateString.substring(0,4),
                dateString.substring(5,7) - 1,
                dateString.substring(8,10)
            );

            var yearDob = dob.getYear();
            var monthDob = dob.getMonth();
            var dateDob = dob.getDate();

            let yearAge = yearNow - yearDob;

            if (monthNow >= monthDob)
                var monthAge = monthNow - monthDob;
            else {
                yearAge--;
                var monthAge = 12 + monthNow - monthDob;
            }

            if (dateNow >= dateDob)
                var dateAge = dateNow - dateDob;
            else {
                monthAge--;
                var dateAge = 31 + dateNow - dateDob;

                if (monthAge < 0) {
                    monthAge = 11;
                    yearAge--;
                }
            }

            let age = {
                years: yearAge,
                months: monthAge,
                days: dateAge
            };
          console.log(age.years)
           this.contactForm.patchValue({
             age:age.years
           })
  }

  createContactForm(){
    this.contactForm = this.formBuilder.group({
      patientName: new FormControl('', [Validators.required]),  
            date: [formatDate(this.today, 'yyyy-MM-dd', 'en')],
            dob: "",
            age: "",
            sex: "",
            programSite: "",
            proof: new FormGroup({
              proofName:new FormControl(''),
              proofId  :new FormControl('')
            }),
            phone : new FormGroup({
              fatherMobile: new FormControl(''),
              motherMobile: new FormControl(''),
              otherMobile: new FormControl(''),
              fatherWA: new FormControl(false),
              motherWA: new FormControl(false),
              otherWA: new FormControl(false)
            }),
            contactInfo: new FormGroup ({
            accompinedBy:new FormControl(''),
            relationship: new FormControl(''),
            motherName: new FormControl(''),
            fatherName: new FormControl(''),
            address: new FormControl(''),
            city: new FormControl(''),
            state: new FormControl(''),
            postalCode: new FormControl(''),
            landMark: new FormControl(''),
            distanceTravelled: new FormControl(''),
            travelledBy: new FormControl(''),
            email: new FormControl(''),
            accomodation: new FormControl(false)
            })
    });
  }

  onSubmit() {
    this.submitted = true;
      console.log('Your form data : ', this.contactForm.value );
      this.httpClientService.addPatient(this.contactForm.value).subscribe((response) => { 
       console.log(response);
       this.router.navigate(['patientslist']);
      },
      (error) => {
        console.log(error);
        alert('Error Found!');
      });;
      // stop here if form is invalid
      if (this.contactForm.invalid) {
        return;
    }

    // display form values on success
    alert('SUCCESS!! :-)\n\n Patient Created Successfully!!!');
  }

}
