import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddPatientComponent } from './add-patient/add-patient.component';
import { AddRoomComponent } from './add-room/add-room.component';
import { AnesthesiaEditComponent } from './anesthesia-edit/anesthesia-edit.component';
import { AnesthesiaListComponent } from './anesthesia-list/anesthesia-list.component';
import { CounselingComponent } from './counseling/counseling.component';
import { DoctorsListComponent } from './doctors-list/doctors-list.component';
import { EditPatientComponent } from './edit-patient/edit-patient.component';
import { EditVisitComponent } from './edit-visit/edit-visit.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { OperationEditComponent } from './operation-edit/operation-edit.component';
import { OperationListComponent } from './operation-list/operation-list.component';
import { PatientsListComponent } from './patients-list/patients-list.component';
import { PediatricEditComponent } from './pediatric-edit/pediatric-edit.component';
import { PediatricListComponent } from './pediatric-list/pediatric-list.component';
import { RoomAllotmentComponent } from './room-allotment/room-allotment.component';
import { RoomDetailsComponent } from './room-details/room-details.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { StaffListComponent } from './staff-list/staff-list.component';
import { SurgeonEditComponent } from './surgeon-edit/surgeon-edit.component';
import { SurgeonListComponent } from './surgeon-list/surgeon-list.component';
import { SurgicalEditComponent } from './surgical-edit/surgical-edit.component';
import { SurgicalListComponent } from './surgical-list/surgical-list.component';
import { TheatreScheduleComponent } from './theatre-schedule/theatre-schedule.component';
import { AuthGuard } from './helper/auth.guard'
import { LabListComponent } from './lab-list/lab-list.component';
import { LabDetailsComponent } from './lab-details/lab-details.component';

const routes: Routes = [
  { path: 'home',component: HomeComponent},
  { path: 'login', component: LoginComponent},
  { path: '', canActivate: [AuthGuard] ,component: SidebarComponent, 
  children: [
  { path: 'doctorslist', component: DoctorsListComponent},
  { path: 'patientslist', component: PatientsListComponent},
  { path: 'pediatriclist', component: PediatricListComponent},
  { path: 'surgeonlist', component: SurgeonListComponent},
  { path: 'anesthesialist', component: AnesthesiaListComponent},
  { path: 'add', component: AddPatientComponent},
  { path: 'edit/:id', component: EditPatientComponent},
  { path: 'edit-visit/:id/:visitId', component: EditVisitComponent},
  { path: 'pediatricedit/:id/:pediatricId/:visitId', component: PediatricEditComponent},
  { path: 'surgeonedit/:id/:surgonId/:visitId', component: SurgeonEditComponent},
  { path: 'anesthesiaedit/:id/:anesthesiaId/:visitId', component: AnesthesiaEditComponent},
  { path: 'operativelist', component: OperationListComponent},
  { path: 'operativeedit/:id/:operativePlanId', component: OperationEditComponent},
  { path: 'roomlist', component: RoomAllotmentComponent},
  { path: 'addromm', component: AddRoomComponent},
  { path: 'roomedit/:id', component: RoomDetailsComponent},
  { path: 'surgicallist', component: SurgicalListComponent},
  { path: 'surgicaledit/:id/:surgicalAppointment/:visitId', component: SurgicalEditComponent},
  { path: 'staffs', component: StaffListComponent},
  { path: 'schedule', component: TheatreScheduleComponent},
  { path: 'counseling', component: CounselingComponent},
  { path: 'lab', component: LabListComponent},
  { path: 'labedit/:id/:labId/:visitId', component: LabDetailsComponent}
] }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
