import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Room } from '../json/room';
import { HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-room-details',
  templateUrl: './room-details.component.html',
  styleUrls: ['./room-details.component.css']
})
export class RoomDetailsComponent implements OnInit {

  patient: any;
  patientId: string;
  _id: string;
  list: any;

  constructor(private httpClientService:HttpClientService, private route: ActivatedRoute,
    public router: Router) { }

  ngOnInit(): void {
    this.patientId = this.route.snapshot.params.id;
    this._id=this.route.snapshot.params.id;
    this.getRoomById(this._id);
  }

  userModel = new Room('', '', '', '', '', '','');

  getRoomById(id){
    this.httpClientService.getRoomById(id).subscribe(data => {
      this.userModel = data.result,
        console.log(this.userModel);
    });
  }

  onSubmit(){
    console.log(this.userModel);
    let notes = { patientId: this.patientId, ... this.userModel };
    console.log(notes);
    this.httpClientService.addRoomById(notes).subscribe(data => {
         
      console.log(data);
      this.router.navigate(['roomlist']);
     });;
  }

}
